import { Component, Input, ChangeDetectionStrategy } from '@angular/core';
import { DocumentViewerOptions, Styles } from '../../../model/UI/document-viewer-commons';

@Component({
  selector: 'bb-document-viewer-backdrop',
  templateUrl: './document-viewer-backdrop.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DocumentViewerBackdropComponent {
  @Input() options?: DocumentViewerOptions;

  get containerStyles(): Styles | null {
    return this.options?.container?.styles || null;
  }

  get hasCustomStyles(): boolean {
    return Boolean(this.options?.container?.styles);
  }
}
